body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif;
}

.app {
  width: 80vw;
  margin: auto;
  font-size: 3vh;
}

.messages {
  list-style: none;
  margin: 0;
  padding: 0;
}

.message {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: top;
      align-items: top;
  margin: 2vh;
  opacity: 0;
  -webkit-transition: opacity 650ms ease;
  -o-transition: opacity 650ms ease;
  transition: opacity 650ms ease;
}

.message.fade-in {
  opacity: 1;
}

.avatar {
  width: 15vh;
  height: 15vh;
  background-size: cover;
  border-radius: 50%;
  margin: 2vh;
}

.text {
  -ms-flex: 1 1;
      flex: 1 1;
  padding-top: 1em;
}

.link {
  text-align: center;
  margin: 5vh;
}

.link a {
  color: #e94949;
  text-decoration: none;
}



